const uuid = require("uuid/v1");

class Transaction {
  constructor({ senderWallet, receiver, amount }) {
    this.id = uuid();
    this.outputMap = this.createOutputMap({ senderWallet, receiver, amount });
    this.input = this.createInput({ senderWallet, outputMap: this.outputMap });
  }

  static validateTransaction(transaction) {
    const {
      input: { address, amount, signature },
      outputMap
    } = transaction;

    const outputTotal = Object.values(outputMap).reduce(
      (total, outputAmount) => total + outputAmount
    );

    if (amount !== outputTotal) {
      console.log("Log: Invalid transaction from " + address);
      return false;
    }
    return true;
  }

  createOutputMap({ senderWallet, receiver, amount }) {
    const outputMap = {};

    outputMap[receiver] = amount;
    outputMap[senderWallet.publicKey] = senderWallet.balance - amount;

    return outputMap;
  }

  createInput({ senderWallet, outputMap }) {
    return {
      timestamp: Date.now(),
      amount: senderWallet.balance,
      address: senderWallet.publicKey,
      signature: senderWallet.sign(outputMap)
    };
  }
}

module.exports = Transaction;
